
.navi-bar{
    position: absolute;
    top: 30px;left: 0px;
    width: 100%;
    display: flex;
    z-index: 110;
    -webkit-box-pack: justify;
    justify-content: space-between;
    padding-left: 40px;
}
.back{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    width: 34px;height: 34px;
    border: 0px;border-radius: 100%;
    text-align: center;
    background:rgba(0,0,0,0.4);
    color: white;
}
.back-i{
    font-size: 25px;
}
.con-img{
    height: 500px;
    position: relative;
    width: 100%;
}
img{
    width: 375px;
}
